import React, { useEffect, useState } from "react";
import { Link, useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
//import './SearchResults.css'; // Import your CSS file for styling

const SearchResults = () => {
  const [posts, setPosts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const location = useLocation();
  const navigate = useNavigate();
  const postsPerPage = 6;

  // Extract query from URL
  const query = new URLSearchParams(location.search).get("query");

  useEffect(() => {
    const fetchSearchResults = async () => {
      try {
        if (query) {
          const response = await axios.get(
            `https://meetanshi.com/blog/wp-json/wp/v2/posts?search=${query}&_embed&per_page=${postsPerPage}&page=${currentPage}`,
          );
          setPosts(response.data);
          const totalPosts = response.headers["x-wp-total"];
          setTotalPages(Math.ceil(totalPosts / postsPerPage));
        }
      } catch (error) {
        console.error("Error fetching search results:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchSearchResults();
  }, [query, currentPage]);

  const handlePageClick = (pageNumber) => {
    setCurrentPage(pageNumber);
    navigate(`${location.pathname}?query=${query}&page=${pageNumber}`);
  };

  const getAuthorLink = (post) => {
    const author =
      post._embedded && post._embedded.author && post._embedded.author[0];
    if (author) {
      const authorImage = author.avatar_urls
        ? author.avatar_urls["96"]
        : "https://via.placeholder.com/50";
      return (
        <Link to={`/author/${author.slug}`} className="author-link">
          <img
            src={authorImage}
            alt={author.name}
            style={{
              width: "30px",
              height: "30px",
              borderRadius: "50%",
              marginRight: "10px",
            }}
          />
          {author.name}
        </Link>
      );
    }
    return "Unknown Author";
  };

  const renderPagination = () => {
    const paginationItems = [];

    if (currentPage > 1) {
      paginationItems.push(
        <li className="page-item" key="prev">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage - 1)}
          >
            Previous
          </button>
        </li>,
      );
    }

    if (currentPage !== 1) {
      paginationItems.push(
        <li
          key={1}
          className={`page-item ${currentPage === 1 ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageClick(1)}>
            1
          </button>
        </li>,
      );
    }

    if (currentPage > 3) {
      paginationItems.push(
        <li key="ellipsis-prev" className="page-item disabled">
          <span className="page-link">...</span>
        </li>,
      );
    }

    if (currentPage > 2) {
      paginationItems.push(
        <li key={currentPage - 1} className="page-item">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage - 1)}
          >
            {currentPage - 1}
          </button>
        </li>,
      );
    }

    paginationItems.push(
      <li key={currentPage} className="page-item active">
        <span className="page-link">{currentPage}</span>
      </li>,
    );

    if (currentPage < totalPages - 1) {
      paginationItems.push(
        <li key={currentPage + 1} className="page-item">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage + 1)}
          >
            {currentPage + 1}
          </button>
        </li>,
      );
    }

    if (currentPage < totalPages - 2) {
      paginationItems.push(
        <li key="ellipsis-next" className="page-item disabled">
          <span className="page-link">...</span>
        </li>,
      );
    }

    if (currentPage !== totalPages) {
      paginationItems.push(
        <li
          key={totalPages}
          className={`page-item ${currentPage === totalPages ? "active" : ""}`}
        >
          <button
            className="page-link"
            onClick={() => handlePageClick(totalPages)}
          >
            {totalPages}
          </button>
        </li>,
      );
    }

    if (currentPage < totalPages) {
      paginationItems.push(
        <li className="page-item" key="next">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage + 1)}
          >
            Next
          </button>
        </li>,
      );
    }

    return paginationItems;
  };

  return (
    <div className="search-results">
      <div className="container-lg container-fluid-md">
        <div className="row">
          <div className="col-md-12">
            <div className="cat-hero-section">
              <div className="home-hero-wrapper text-center">
                <h2 className="home-heading">Search Results for: "{query}"</h2>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        {loading ? (
          <p className="text-center">Loading...</p>
        ) : (
          <div className="row mt-5 regular-post-main">
            {posts.length > 0 ? (
              posts.map((post) => (
                <div
                  className="col-md-6 text-left regular-post-col mb-5"
                  key={post.id}
                >
                  <div className="regular-post-wrapper">
                    <Link className="post-title-link" to={`/post/${post.slug}`}>
                      <h3
                        className="post-title"
                        dangerouslySetInnerHTML={{
                          __html: post.title.rendered,
                        }}
                      ></h3>
                    </Link>
                    <p
                      dangerouslySetInnerHTML={{
                        __html: post.excerpt.rendered,
                      }}
                    ></p>
                    <p className="author-name-date">
                      <span>{getAuthorLink(post)}</span>
                      <span>
                        {new Date(post.date).toLocaleDateString("en-US", {
                          year: "numeric",
                          month: "short",
                          day: "numeric",
                        })}
                      </span>
                    </p>
                  </div>
                </div>
              ))
            ) : (
              <p className="text-center">No results found for "{query}".</p>
            )}
          </div>
        )}

        {totalPages > 1 && (
          <nav aria-label="Page navigation">
            <ul className="pagination justify-content-center">
              {renderPagination()}
            </ul>
          </nav>
        )}
      </div>
    </div>
  );
};

export default SearchResults;
