import React, { useState, useEffect } from "react";
import axios from "axios";
import { Link, useNavigate, useLocation } from "react-router-dom";
import Cookies from "js-cookie";

// Skeleton Loader for Post
const SkeletonPost = () => {
  return (
    <div className="skeleton-post-wrapper">
      <div className="skeleton-title"></div>
      <div className="skeleton-excerpt"></div>
      <div className="skeleton-author"></div>
    </div>
  );
};

// Skeleton Loader for Sticky Post
const SkeletonStickyPost = () => {
  return (
    <div className="skeleton-sticky-post">
      <div className="skeleton-title"></div>
      <div className="skeleton-excerpt"></div>
    </div>
  );
};

// Skeleton Loader for Pagination
const SkeletonPagination = () => {
  return (
    <div className="skeleton-pagination">
      <div className="skeleton-page"></div>
      <div className="skeleton-page"></div>
      <div className="skeleton-page"></div>
    </div>
  );
};

const Posts = () => {
  const [posts, setPosts] = useState([]);
  const [stickyPosts, setStickyPosts] = useState([]);
  const [loading, setLoading] = useState(true); // Add loading state
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const navigate = useNavigate();
  const location = useLocation();

  const postsPerPage = 10;

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const response = await axios.get(
          `https://meetanshi.com/blog/wp-json/wp/v2/posts?_embed&per_page=${postsPerPage}&page=${currentPage}`,
        );
        setPosts(response.data);
        Cookies.set("posts", JSON.stringify(response.data), {
          expires: 1,
          path: "/",
        });
        const totalPosts = response.headers["x-wp-total"];
        setTotalPages(Math.ceil(totalPosts / postsPerPage));
      } catch (error) {
        console.error("Error fetching posts:", error);
      } finally {
        setLoading(false); // Set loading to false after fetch
      }
    };

    const fetchStickyPosts = async () => {
      try {
        const response = await axios.get(
          `https://meetanshi.com/blog/wp-json/wp/v2/posts?_embed&sticky=true`,
        );
        setStickyPosts(response.data);
      } catch (error) {
        console.error("Error fetching sticky posts:", error);
      }
    };

    fetchPosts();
    fetchStickyPosts();
  }, [currentPage]);

  const getAuthorLink = (post) => {
    const author = post._embedded?.author?.[0];
    if (author) {
      const authorImage =
        author.avatar_urls?.["96"] || "https://via.placeholder.com/50";
      return (
        <Link to={`/author/${author.slug}`} className="author-link">
          <img
            src={authorImage}
            alt={author.name}
            style={{
              width: "30px",
              height: "30px",
              borderRadius: "50%",
              marginRight: "10px",
            }}
          />
          {author.name}
        </Link>
      );
    }
    return "Unknown Author";
  };

  const handlePageClick = (pageNumber) => {
    setCurrentPage(pageNumber);
    navigate(`${location.pathname}?page=${pageNumber}`);
  };

  const renderPagination = () => {
    const paginationItems = [];
    const range = 1;

    if (currentPage > 1) {
      paginationItems.push(
        <li className="page-item" key="prev">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage - 1)}
          >
            Previous
          </button>
        </li>,
      );
    }

    if (totalPages > 0) {
      paginationItems.push(
        <li
          className={`page-item ${currentPage === 1 ? "active" : ""}`}
          key={1}
        >
          <button className="page-link" onClick={() => handlePageClick(1)}>
            1
          </button>
        </li>,
      );
    }

    if (currentPage > range + 2) {
      paginationItems.push(
        <li className="page-item disabled" key="ellipsis1">
          <span className="page-link">...</span>
        </li>,
      );
    }

    for (
      let page = Math.max(2, currentPage - range);
      page <= Math.min(totalPages - 1, currentPage + range);
      page++
    ) {
      paginationItems.push(
        <li
          className={`page-item ${currentPage === page ? "active" : ""}`}
          key={page}
        >
          <button className="page-link" onClick={() => handlePageClick(page)}>
            {page}
          </button>
        </li>,
      );
    }

    if (currentPage < totalPages - range - 1) {
      paginationItems.push(
        <li className="page-item disabled" key="ellipsis2">
          <span className="page-link">...</span>
        </li>,
      );
    }

    if (totalPages > 1) {
      paginationItems.push(
        <li
          className={`page-item ${currentPage === totalPages ? "active" : ""}`}
          key={totalPages}
        >
          <button
            className="page-link"
            onClick={() => handlePageClick(totalPages)}
          >
            {totalPages}
          </button>
        </li>,
      );
    }

    if (currentPage < totalPages) {
      paginationItems.push(
        <li className="page-item" key="next">
          <button
            className="page-link"
            onClick={() => handlePageClick(currentPage + 1)}
          >
            Next
          </button>
        </li>,
      );
    }

    return paginationItems;
  };

  return (
    <div>
      {loading ? ( // Show skeleton loader if still loading
        <div>
          {/* Skeleton Loader for Sticky Posts */}
          <div className="sticky-post-home">
            {Array.from({ length: 1 }).map((_, index) => (
              <SkeletonStickyPost key={index} />
            ))}
          </div>
          {/* Skeleton Loader for Regular Posts */}
          <div className="row mt-5 regular-post-main">
            {Array.from({ length: 10 }).map((_, index) => (
              <div
                className="col-md-6 text-left regular-post-col mb-5"
                key={index}
              >
                <SkeletonPost />
              </div>
            ))}
          </div>
          <SkeletonPagination />
        </div>
      ) : (
        <>
          {/* Sticky Post Section */}
          {stickyPosts.length > 0 && (
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  <div className="sticky-post-home">
                    {stickyPosts.map((post) => (
                      <div
                        key={post.id}
                        className="sticky-post-item text-center"
                      >
                        <p className="author-name-date">
                          <span>{getAuthorLink(post)}</span>{" "}
                          <span>
                            {new Date(post.date).toLocaleDateString("en-US", {
                              year: "numeric",
                              month: "short",
                              day: "numeric",
                            })}
                          </span>
                        </p>
                        <Link
                          className="sticky-post-title-link"
                          to={`/post/${post.slug}`}
                        >
                          <h2
                            className="sticky-post-title"
                            dangerouslySetInnerHTML={{
                              __html: post.title.rendered,
                            }}
                          ></h2>
                        </Link>
                        <p
                          dangerouslySetInnerHTML={{
                            __html: post.excerpt.rendered,
                          }}
                        ></p>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Regular Posts Section */}
          <div className="row mt-5 regular-post-main">
            {posts.map((post) => (
              <div
                className="col-md-6 text-left regular-post-col mb-5"
                key={post.id}
              >
                <div className="regular-post-wrapper">
                  <Link className="post-title-link" to={`/post/${post.slug}`}>
                    <h3
                      className="post-title"
                      dangerouslySetInnerHTML={{ __html: post.title.rendered }}
                    ></h3>
                  </Link>
                  <p
                    dangerouslySetInnerHTML={{ __html: post.excerpt.rendered }}
                  ></p>
                  <p className="author-name-date">
                    <span>{getAuthorLink(post)}</span>{" "}
                    <span>
                      {new Date(post.date).toLocaleDateString("en-US", {
                        year: "numeric",
                        month: "short",
                        day: "numeric",
                      })}
                    </span>
                  </p>
                </div>
              </div>
            ))}
          </div>

          {/* Pagination Section */}
          {totalPages > 1 && (
            <nav aria-label="Page navigation" className="pagination-main mt-3">
              <ul className="pagination justify-content-center">
                {renderPagination()}
              </ul>
            </nav>
          )}
        </>
      )}
    </div>
  );
};

export default Posts;
